@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap');

:root{
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body{
  font-family: "Noto Serif";
  color:cornflowerblue;
  background-color:rgb(255, 255, 255);
}
.App{
  max-width: 80%;
  margin: 0 auto;
}

.logo{
  width: 12%;
  height: 17vh;
}


.title h1{
  color: var(--error);
  font-size: 1.5rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2, .title p{
  text-align: center;
  font-size: 2.5rem;
  margin: 1% 0%;
}
.title h2{
  margin-top: -4%;
}

/* upload form styles */
form{
  margin: 2% auto 1%;
  text-align: center;
}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}
label{
  display: block;
  width: 70px;
  height: 70px;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 65px;
  color: var(--primary);
  font-weight: bold;
  font-size: 5rem;
}
label:hover{
  background: var(--primary);
  color: white;
}
.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color: var(--error);
}



/* progress bar styles */
.progress-bar {
  height: 1.5rem;
  background-color:rgb(253, 122, 0);
  margin-top:1.5rem;
}

.progress-bar .progressH2 {
  color: #000;
}


/* image grid styles */
.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.5;
}
.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
}

/* modal styles */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 70%;
  max-height: 80%;
  margin: 20px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 5px solid cornflowerblue;
}
